import { inject, injectable } from "../domain/core/diContainer";
import { IInlineEditor } from "../domain/infrastructure/IInlineEditor";
import { IModelModifyInfo } from "../domain/model/models";
import { IViewModel } from "../domain/viewModel/IViewModel";


@injectable()
export class InlineEditorImpl implements IInlineEditor {
    private root: HTMLDivElement = document.createElement("div");
    private attachedElement?: HTMLElement;
    private resizeObserver?: ResizeObserver;
    private connected: boolean = false;


    constructor(
        @inject(IViewModel) private readonly viewModel: IViewModel,
    ) { }

    public get Root() { return this.root; }


    public connect(editorName: string): void {
        if (this.connected) { return; }

        this.root.classList.add(editorName + "-editor-root");
        document.body.appendChild(this.root);
        this.connected = true;
    }
    public attach(editorName: string, element: HTMLElement): void {
        this.connect(editorName);
        const fStyle = this.root.style;
        fStyle.position = "absolute";
        fStyle.margin = "0px";
        fStyle.border = "0px";
        fStyle.overflowY = "auto"; // TODO: Tum editor divlerine ekliyor. Ileride degisebilir.
        this.detach();
        this.attachedElement = element;
        this.resizeObserver = new ResizeObserver(entries => {
            if (fStyle.display == "none") { return; }
            this.resizeTo(element);
        });
        this.resizeObserver.observe(element);
        this.resizeTo(element);
    }
    public detach(): void {
        if (!this.attachedElement) { return; }

        this.resizeObserver?.disconnect();
        this.show(false);
    }
    public show(show: boolean): void {
        if (!this.attachedElement) { return; }

        if (show) {
            this.resizeTo(this.attachedElement!);
        }

        this.root.style.display = show ? "block" : "none";
    }
    private resizeTo(element: HTMLElement) {
        const fStyle = this.root.style;
        const rect = element.getBoundingClientRect();
        fStyle.left = rect.left + "px";
        fStyle.top = rect.top + "px";
        fStyle.width = rect.width + "px";
        fStyle.height = rect.height + "px";
    }

    public modified(modifyInfo: IModelModifyInfo) {
        const currentItem = this.viewModel.studio.currentItem;
        if (!currentItem) {
            return;
        }

        if (modifyInfo.model) { currentItem.modified.model = modifyInfo.model; }
        if (modifyInfo.state) { currentItem.modified.state = modifyInfo.state; }
    }
}