import { appSettings } from "./appsetting";
import { UrlHelper } from "./common/urlHelper";
type EnvType = keyof typeof appSettings;
const environment = UrlHelper.gatherQueryString<{environment?: EnvType}>().environment || "";
export const presentationLayer: "vue3" | "html" /* | "react" | "vue" */ = "vue3";
export const version = "0.0.58"; //DO NOT MODIFY!! THIS LINE IS AUTOMATED!!!

const hostName = window.location.hostname;
export const startupEnvironment: EnvType = environment || (Object.keys(appSettings) as Array<EnvType>).find(envName => {
    return appSettings[envName as EnvType].hostnames.find(name => hostName.endsWith(name))
}) || "default";