import { inject, injectable } from "../domain/core/diContainer";
import { IConfig } from "../domain/infrastructure/IConfig";
import { IExporterApi } from "../domain/infrastructure/IExporterApi";
import { ILogger } from "../domain/infrastructure/ILogger";
import { INetwork, IResponse, Method } from "../domain/infrastructure/INetwork";
import { createError } from "../domain/model/shellError";

@injectable()
export class exporterApiImpl implements IExporterApi {
    private exporterApiUrl: string;

    constructor(
        @inject(IConfig) private readonly config: IConfig,
        @inject(ILogger) private readonly logger: ILogger,
        @inject(INetwork) private readonly network: INetwork,
    ) {
        this.exporterApiUrl = this.config.getValue("exporterApiUrl");
    }

    private async request<TResponse extends Record<string, any> | void | string>(method: Method, path: string, data: Record<string, any> | FormData | undefined, options?: { timeout?: number }): Promise<TResponse> {
        const url = this.network.join(this.exporterApiUrl, path);

        let resp: IResponse<TResponse>;
        try {
            resp = await this.network.request<TResponse, typeof data>({ url, body: data, withCredentials: true, timeout: options?.timeout, method });
        } catch (ex) {
            const err = ex as { response?: IResponse<any> } & Error;
            const resp = err.response;
            if (!resp) {
                const message = "Service is unavailable now";
                this.logger.log({ level: "error", message });
                throw createError({ message, type: "technical", preventBackendLog: true });
            }

            const message = err.message || (resp.status + " " + resp.statusText);
            this.logger.log({ level: "error", message });
            throw createError({ message, type: "technical" });
        }

        return resp.body;
    }

    public async go() {
        await this.request<{ message: string }>("POST", "/go", undefined);
    }
}