import { UrlHelper } from "../../common/urlHelper";
import { inject, injectable } from "../../domain/core/diContainer";
import { IApplication } from "../../domain/model/models";
import { IRouter } from "../../domain/presentation/IRouter";
import { IAutoLogin, IAutoLoginData } from "../../domain/useCase/IAutoLogin";
import { IViewModel } from "../../domain/viewModel/IViewModel";

@injectable()
export class AutoLogin implements IAutoLogin {
    constructor(
        @inject(IViewModel) private readonly viewModel: IViewModel,
        @inject(IRouter) private readonly router: IRouter,
    ) { }
    public gatherAutoLoginData() {
        this.viewModel.autoLogin = UrlHelper.gatherQueryString<IAutoLoginData>();
        return this.viewModel.autoLogin;
    }
    public trySetDomain(autologinData: IAutoLoginData) {
        if (!autologinData.domain) { return; }

        const autoDomain = this.viewModel.user?.domains.find(domain => domain.name == autologinData.domain);
        if (autoDomain) {
            this.viewModel.user!.selectedDomain = autoDomain;
        }
    }
    public allSystemsGo(autoLoginData: IAutoLoginData) {
        const retVal = autoLoginData.domain && autoLoginData.email && autoLoginData.pass ? true : false;
        if (retVal) { autoLoginData.domain = autoLoginData.email = autoLoginData.pass = undefined; }
        return retVal;
    }
    public tryGoApp(apps: Array<IApplication>): void {
        const autoLoginData = this.viewModel.autoLogin;
        if (!autoLoginData) { return; }

        this.viewModel.autoLogin = undefined;
        const targetApp = apps.find(app => app.name == autoLoginData.app || app.ID == autoLoginData.app);
        if (!targetApp) { return; }

        this.router.push(`/studio/${targetApp.ID}`);
    }
}