import { ModelType } from "@stechquick/algae/lib/qCloudTemp/quickCloud";
import { injectable } from "../../domain/core/diContainer";
import { IModel } from "../../domain/model/models";

@injectable()
export class ModelBodyHelperImpl {
    constructor() { }

    getModelBody(model: IModel, bodyKey: ModelType) {
        if (!model.modelBody) { return undefined; }
        const index = model.modelBody.findIndex(body => body.key == bodyKey);
        if (index == -1) { return undefined; }
        return model.modelBody[index];
    }
}