import { container, inject, injectable } from "../../domain/core/diContainer";
import { IFolder, IModel, IModule } from "../../domain/model/models";
import { IAsyncComponentCreator } from "../../domain/presentation/IAsyncComponentCreator";
import { IDialog } from "../../domain/presentation/IDialog";
import { ITreeItem } from "../../domain/presentation/ITreeView";
import { IModelSelector } from "../../domain/useCase/IModelSelector";
import { IUseCaseExecutor } from "../../domain/useCase/IUseCaseExecutor";
import { IViewModel } from "../../domain/viewModel/IViewModel";

@injectable()
export class ModelSelector implements IModelSelector {
    constructor(
        @inject(IViewModel) private readonly viewModel: IViewModel,
        @inject(IUseCaseExecutor) private readonly executor: IUseCaseExecutor,
        @inject(IAsyncComponentCreator) private readonly compCreator = container.resolve<IAsyncComponentCreator>(IAsyncComponentCreator),
    ) { }


    private selectCb?: (item: IModel) => void;
    private dialogID?: string;

    public openModelSelector(cb: (item: IModel) => void) {
        this.selectCb = cb;
        const items = this.filterModels(this.viewModel.studio.items);
        const msItems = this.convertToTreeItems(items);

        const dialog = container.resolve<IDialog>(IDialog);
        this.dialogID = dialog.showDialog(this.compCreator.modelSelectionWizard(), {
            closable: true,
            width: "800px",
            title: "Select Page"
        }, { msItems });
    }

    private filterModels(items: Array<IModel | IModule | IFolder>): Array<IModel | IModule | IFolder> {
        const newItems: Array<IModel | IModule | IFolder> = [];
        items.forEach(i => {
            if (i.objectType != "model" && i.children) {
                i.children = this.filterModels(i.children) as Array<IModel | IFolder>;
            }

            if (i.objectType == "model" && (i.modelType != "qjson" || i.usageType == "appSettings")) {
                return;
            }

            newItems.push(i);
        });

        return newItems;
    }


    private convertToTreeItems(items: Array<IModule | IFolder | IModel>) {
        const converter = (items: Array<IModule | IFolder | IModel>) => items.map(item => {
            const treeItem: ITreeItem = { id: item.ID, name: item.name, typeIcon: { icon: '' }, actions: [] };
            switch (item.objectType) {
                case "folder":

                case "module":
                    treeItem.typeIcon.icon = 'mdi mdi-puzzle';
                    treeItem.children = converter(item.children);
                    return treeItem;
                case "model":
                    if (item.modelType == 'qjson' || item.modelType == 'js' || item.modelType == 'yaml') {
                        treeItem.typeIcon.icon = 'mdi mdi-palette';
                        treeItem.typeIcon.color = '#449DD1';
                    }
                    if (item.modelType == 'entityDesigner') {
                        treeItem.typeIcon.icon = 'mdi mdi-database ';
                        treeItem.typeIcon.color = '#E9724C';
                    }
                    if (item.modelType == 'bpmn') {
                        treeItem.typeIcon.icon = 'mdi mdi-vector-polyline';
                        treeItem.typeIcon.color = '#007C77';
                    }

                    if (item.modelType == 'process') {
                        treeItem.typeIcon.icon = 'mdi mdi-auto-fix';
                        treeItem.typeIcon.color = '#007C77';
                    }

                    treeItem.cb = {
                        select: this.executor.wrap(() => this.onItemSelect(item), { loading: true }),
                    }
                    return treeItem;
            }
        });

        const retVal = converter(items);
        return retVal;
    }

    private async onItemSelect(item: IModel) {
        this.selectCb!(item);
        const dialog = container.resolve<IDialog>(IDialog);
        dialog.closeDialog(this.dialogID!);
    }

}