import { container, injectable } from "../../../domain/core/diContainer";
import { IInlineEditor } from "../../../domain/infrastructure/IInlineEditor";
import { IFolder, IModel, IModelBodyObject, IModelModifyInfo, IModule } from "../../../domain/model/models";
import { IProcessEditor } from "../../../domain/objects/editors/processEditor/IProcessEditor";
import { IAsyncComponentCreator } from "../../../domain/presentation/IAsyncComponentCreator";
import { IProcessEditorState } from "../../../domain/presentation/editors/IProcessEditorModel";
import { IQCloudApi } from "../../../domain/infrastructure/IQCloudApi";
import { ModifiedCallbackType } from "../../../domain/model/editors/modifiedCallbackType";
import { IProcessEditorUtil } from "../../../domain/useCase/IProcessEditorUtil";
import { IProcessModel } from "@stechquick/algae/lib/qCloudTemp/process";
export interface IProcessEditorModel {
    model: IProcessModel;
    state: IProcessEditorState;
}
export interface IProcessEditorProps extends Record<string, unknown> {
    name: string;
    functions: {
        setModel?: (model: IProcessModel, module: IModule, state: IProcessEditorState) => Promise<void>;
        getModel?: () => Promise<{ model: IProcessModel; state: IProcessEditorState }>;
    };
    callbacks: {
        modified: ModifiedCallbackType;
        getBpmnModel: (id: string) => Promise<string>;
    };
}
interface IProcessEditorData {
    editor: object;
    props: IProcessEditorProps;
}

@injectable()
export class ProcessEditorImpl implements IProcessEditor {
    public name = "process" as "process";
    private editorData?: IProcessEditorData;
    constructor(
        private readonly compCreator = container.resolve<IAsyncComponentCreator>(IAsyncComponentCreator),
        private readonly inlineEditor = container.resolve<IInlineEditor>(IInlineEditor),
        private readonly qCloudApi = container.resolve<IQCloudApi>(IQCloudApi),
        private readonly processEditorUtil = container.resolve<IProcessEditorUtil>(IProcessEditorUtil)
    ) {}

    public connect() {}
    public attach(element: HTMLElement): void {
        this.inlineEditor.attach(this.name, element);

        const props: IProcessEditorProps = {
            name: "",
            functions: {},
            callbacks: {
                modified: (modifyInfo) => this.inlineEditor.modified(modifyInfo),
                getBpmnModel: (id) => this.getBpmnModel(id),
            },
        };
        const editor = this.compCreator.instantiateComponent(this.compCreator.createProcessEditor(), props, this.inlineEditor.Root);
        this.editorData = { editor, props };
    }
    public detach(): void {
        if (!this.editorData) {
            return;
        }

        this.compCreator.destroyComponent(this.editorData.editor);
        delete this.editorData;
    }
    public show(show: boolean): void {
        this.inlineEditor.show(show);
    }
    public async getModel(): Promise<{ model: IModelBodyObject[]; state: any }> {
        const model = await this.editorData!.props.functions.getModel!();
        return {
            model: [{ key: "process", model: model.model }],
            state: model.state,
        };
    }
    public async setModel(item: IModel) {
        const module = this.getModule(item);

        const emptyState = this.processEditorUtil.getEmptyProcessEditorState();
        const state: IProcessEditorState = {
            stepper: item.state?.stepper || emptyState.stepper,
            existingBpmnVariableCache: item.state?.existingBpmnVariableCache || emptyState.existingBpmnVariableCache,
            creatingProcessFileName: item.state?.creatingProcessFileName || item.name,
        };

        const model = item.modelBody!.reduce<IProcessModel>((model, curModel) => {
            if (curModel.key == "process") {
                model = curModel.model;
            }
            return model;
        }, {} as IProcessModel);
        await this.editorData!.props.functions.setModel!(model, module, state);
    }

    private async getBpmnModel(id: string) {
        const modelBody = await this.qCloudApi.getModelBody(id);
        const model = modelBody.model![0].model;
        return model;
    }

    private getModule(item: IModel) {
        let currentModule: IModule | IFolder = item.parent as IModule | IFolder;
        while (currentModule.objectType !== "module") currentModule = currentModule.parent as IModule | IFolder;
        return currentModule;
    }
}
