import { inject, injectable } from "../domain/core/diContainer";
import { IConfig } from "../domain/infrastructure/IConfig";
import { INetwork, Method } from "../domain/infrastructure/INetwork";
import { IGitResponse, IProviderApi } from "../domain/infrastructure/IProviderApi";
import { IViewModel } from "../domain/viewModel/IViewModel";
import { IQCloudBaseResponse } from "./qCloudApiImpl";


let counter = 0;
@injectable()
export class ProviderApiImpl implements IProviderApi {
    private providerApiUrl: string;

    constructor(
        @inject(IConfig) private readonly config: IConfig,
        @inject(INetwork) private readonly network: INetwork,
        @inject(IViewModel) private viewModel: IViewModel,
    ) {
        this.providerApiUrl = this.config.getValue("providerApiUrl");
    }

    private async request<TResponse extends Record<string, any> | void | string>(path: string, method: Method, data?: Record<string, any>, options?: { timeout: number }){
        const url = this.join(this.providerApiUrl, path);
        const headers = { "provider-auth-cookie": this.viewModel.user!.authorization! };
        const response = await this.network.request<TResponse, typeof data>({url, method, headers, body: data, timeout: options?.timeout, withCredentials: true});
        return response.body;
    }

    private join(...urlParts: Array<string>) {
        return urlParts.map(urlPart => {
            let start = 0, end = urlPart.length, doOp = false;
            if (urlPart[0] == "/") { start++; doOp = true; }
            if (urlPart[urlPart.length - 1] == "/") { end--; doOp = true; }
            return doOp ? urlPart.substring(start, end) : urlPart;
        }).join("/");
    }


    public async getProjectsByGroupId(groupId: number) {
        const projects = await this.request<IQCloudBaseResponse<IGitResponse>>(`/projects/getProjectsByGroupId?groupId=${groupId}`, "GET", {}, {timeout: 50000});
        return projects;
    }

    public async getProjectsOfProduct() {
        const products = await this.request<IQCloudBaseResponse<IGitResponse>>(`/projects/getProjectsOfProduct`, "GET", {}, { timeout: 50000 });
        return products;
    }

    public async getGroupNamesWithId() {
        const gitGroups = await this.request<IQCloudBaseResponse<IGitResponse>>(`/groups/getGroupNamesWithId`, "GET", {}, { timeout: 50000 });
        return gitGroups;
    }
}