import { container, inject } from "../../domain/core/diContainer";
import { IModel } from "../../domain/model/models";
import { IEditor } from "../../domain/objects/editors/IEditor";
import { IEntityDesignerEditor } from "../../domain/objects/editors/IEntityDesignerEditor";
import { IProcessDesignEditor } from "../../domain/objects/editors/IProcessDesignEditor";
import { IProcessEditor } from "../../domain/objects/editors/processEditor/IProcessEditor";
import { IQuickEditor } from "../../domain/objects/editors/IQuickEditor";
import { IEditorManager } from "../../domain/objects/IEditorManager";
import { IFileEditor } from "../../domain/objects/editors/IFileEditor";


export class EditorManager implements IEditorManager {
    constructor(
        private readonly editors = container.resolveAll<IEditor>(IEditor),
    ) { }

    public attach(element: HTMLElement): void {
        this.editors.forEach(editor => editor.attach(element));
        this.reset();
    }
    public detach(): void {
        this.editors.forEach(editor => editor.detach());
    }
    public reset(): void {
        this.editors.forEach(editor => editor.show(false));
    }
    public show(editor: IEditor, show: boolean): void {
        if (show) {
            this.editors.forEach(curEditor => curEditor.show(editor == curEditor));
        } else {
            editor.show(false);
        }
    }
    public getEditor(item: IModel): IEditor | undefined {
        switch (item.modelType) {
            case "entityDesigner":
                return container.resolve<IEntityDesignerEditor>(IEntityDesignerEditor);
            case "js":
                return container.resolve<IFileEditor>(IFileEditor);
            case "namedComponent":
                return undefined;
            case "bpmn":
                return container.resolve<IProcessDesignEditor>(IProcessDesignEditor);
            case "process":
                return container.resolve<IProcessEditor>(IProcessEditor);
            case "qjson":
                return container.resolve<IQuickEditor>(IQuickEditor);
            case "yaml":
                return container.resolve<IFileEditor>(IFileEditor);
            case "css":
                return container.resolve<IFileEditor>(IFileEditor);
        }
    }
}