import { injectable } from "../domain/core/diContainer";
import { IDebounce } from "../domain/infrastructure/IDebounce";

@injectable()
export class DebounceImpl<T extends (...args: any) => any> implements IDebounce<T> {
    private timer?: NodeJS.Timeout;

    debounce(cb: Function, delayTime: number, ...args: any) {
        clearTimeout(this.timer);
        this.timer = setTimeout(() => cb.call(this, ...args), delayTime);
    }

    resetTimer() {
        clearTimeout(this.timer);
    }
}
