import { inject, injectable } from "../../domain/core/diContainer";
import { IQCloudApi } from "../../domain/infrastructure/IQCloudApi";
import { IAsyncComponentCreator } from "../../domain/presentation/IAsyncComponentCreator";
import { IDialog } from "../../domain/presentation/IDialog";
import { INotification } from "../../domain/presentation/INotification";
import { IHistoryUseCase } from "../../domain/useCase/IHistoryUseCase";

@injectable()
export class HistoryUseCaseImpl implements IHistoryUseCase {
    constructor(
        @inject(IQCloudApi) private readonly qcloudApi: IQCloudApi,
        @inject(INotification) private readonly notification: INotification,
        @inject(IDialog) private readonly dialog: IDialog,
        @inject(IAsyncComponentCreator) private readonly compCreator: IAsyncComponentCreator,
    ) { }

    public async openHistoryDialog(modelID: string, modelName: string) {
        const histories = await this.qcloudApi.listModelHistories(modelID);
        if (!histories || histories.length == 0) {
            this.notification.showNotification({ text: "This item has no history yet", type: "info" });
            return;
        }

        this.dialog.showDialog(this.compCreator.createModelHistoriesComponent(),
            { closable: true, title: `Histories for ${modelName}`, closeOnOutClick: true, width: '700px', height: '500px', overflow: 'auto' },
            { histories }
        );
    }
}