import { container, injectable } from "../../../domain/core/diContainer";
import { IInlineEditor } from "../../../domain/infrastructure/IInlineEditor";
import { IModel, IModelBodyObject, IModelModifyInfo } from "../../../domain/model/models";
import { IAsyncComponentCreator } from "../../../domain/presentation/IAsyncComponentCreator";
import { IViewModel } from "../../../domain/viewModel/IViewModel";
import { ModifiedCallbackType } from "../../../domain/model/editors/modifiedCallbackType";
import { IFileEditor, IFileEditorState } from "../../../domain/objects/editors/IFileEditor";


export interface IFileEditorProps extends Record<string, unknown> {
    functions: {
        setModel?: ( model: string, state: IFileEditorState) => Promise<void>;
        getModel?: () => Promise<{ model: string; state: IFileEditorState }>;
    };
    callbacks: {
        modified: ModifiedCallbackType;
    };
    fileType?: "js" | "yaml" |"css";
}
interface IFileEditorData {
    editor: object;
    props: IFileEditorProps;
}

@injectable()
export class FileEditorImpl implements IFileEditor {
    public name = "fileEditor" as "fileEditor";
    private editorData?: IFileEditorData;
    constructor(
        private readonly compCreator = container.resolve<IAsyncComponentCreator>(IAsyncComponentCreator),
        private readonly inlineEditor = container.resolve<IInlineEditor>(IInlineEditor),
        private readonly viewModel = container.resolve<IViewModel>(IViewModel),
    ) {}

    public connect() {}
    public attach(element: HTMLElement): void {
        this.inlineEditor.attach(this.name, element);

        const props: IFileEditorProps = {
            functions: {},
            callbacks: {
                modified: (modifyInfo) => this.inlineEditor.modified(modifyInfo),
            },
        };
        const editor = this.compCreator.instantiateComponent(this.compCreator.createFileEditor(), props, this.inlineEditor.Root);
        this.editorData = { editor, props };
    }
    public detach(): void {
        if (!this.editorData) {
            return;
        }

        this.compCreator.destroyComponent(this.editorData.editor);
        delete this.editorData;
    }
    public show(show: boolean): void {
        this.inlineEditor.show(show);
    }
    public async getModel(): Promise<{ model: IModelBodyObject[]; state: any }> {
        const model = await this.editorData!.props.functions.getModel!();
        return {
            model: [{ key: this.editorData!.props.fileType!, model: model.model }],
            state: model.state,
        };
    }
    public async setModel(item: IModel) {
        const state: IFileEditorState = item.state ? {...item.state, checkout: item.checkedOut} : { scrollTop: 0, selectionEnd: 0, selectionStart: 0, checkout: item.checkedOut };

        const model = item.modelBody!.reduce<string>((model, curModel) => {
            switch (curModel.key) {
                case "yaml":
                case "js":
                case "css":
                    this.editorData!.props.fileType = curModel.key;
                    model = curModel.model;
                    break;
            }
            return model;
        }, "" as string);
        await this.editorData!.props.functions.setModel!(model, state);
    }
}
