import { injectable, inject } from "../../domain/core/diContainer";
import { IExportItem } from "@stechquick/algae/lib/qCloudTemp/exporter";
import { IExportViewItem, IModel } from '../../domain/model/models';
import { createError } from "../../domain/model/shellError";
import { IJobUtils } from "../../domain/useCase/IJobUtils";

@injectable()
export class JobUtilsImpl implements IJobUtils {
    private settingTypeMapper(fileName: string): IExportItem["type"] {
        switch (fileName) {
            case "Pipeline": return "pipeline_qjson";
            case "Alert Component": return "alert_qjson";
            case "Loading Component": return "loading_qjson";
            case "Container Services File": return "containerServices_js";
            case "Settings File": return "settings_yaml";
            case "Local Proxy File": return "localProxy_yaml";
            case "Style File": return "css";
            case "Global Localization": return "globalLocalization_qjson";
            default: throw createError({ message: `There is not a type of 'IExportItem.type' map!`, type: "business", });
        }
    }

    public getItemType(item: IExportViewItem | IModel): IExportItem["type"] {
        if (item.usageType == "appSettings") {
            return this.settingTypeMapper(item.name);
        }

        const itemType = (item as any)["type"] ? (item as IExportViewItem).type : (item as IModel).modelType;

        switch (itemType) {
            case "bpmn": return "bpmn";
            case "process": return "process";
            case "entityDesigner": return "entityDesigner";
            case "qjson": return "qjsons";
            case "namedComponent": return "namedComps";
        }

        throw createError({ message: `There is no type to choose for export!`, type: "business", });
    }

    public getFileFullname(item: IExportViewItem | IModel) {
        if(item.usageType == "appSettings") {
            switch (item.name) {
                case "Pipeline": return "pipeline.qjson";
                case "Alert Component": return "alert.qjson";
                case "Loading Component": return "loading.qjson";
                case "Container Services File": return "containerServices.js";
                case "Settings File": return "settings.yaml";
                case "Local Proxy File": return "localProxy.yaml";
                case "Style File": return "main.css";
                case "Global Localization": return "globalLocalization.qjson";
            }
        }

        const itemType = (item as any)["type"] ? (item as IExportViewItem).type : (item as IModel).modelType;

        switch (itemType) {
            case "qjson": return `${item.name}.qjson`;
            case "bpmn": return `${item.name}.bpmn`;
            case "process": return `${item.name}.process`;
            case "entityDesigner": return `${item.name}.dm`;
        }

        throw createError({ message: `There is not a type of 'IExportItem.type' map!`, type: "business", });
    }
}