import { ModelType } from "@stechquick/algae/lib/qCloudTemp/quickCloud";
import { inject, injectable } from "../../domain/core/diContainer";
import { IQCloudApi } from "../../domain/infrastructure/IQCloudApi";
import { IApplication, IFolder, IModel, IModule } from "../../domain/model/models";
import { createError } from "../../domain/model/shellError";
import { INotification } from "../../domain/presentation/INotification";
import { ICreateModel, ICreateModelParam } from "../../domain/useCase/ICreateModel";
import { IFileExplorer } from "../../domain/useCase/IFileExplorer";
import { IViewModel } from "../../domain/viewModel/IViewModel";
import { IViewModelManager } from "../../domain/viewModel/IViewModelManager";

const modelFilterType: Record<ModelType, "entity" | "ui" | "process"> = {
    "bpmn": "process",
    "css": "ui",
    "entityDesigner": "entity",
    "js": "ui",
    "namedComponent": "ui",
    "process": "process",
    "qjson": "ui",
    "yaml": "ui",
};
@injectable()
export class CreateModelImpl implements ICreateModel {
    private model?: IModel;
    constructor(
        @inject(IQCloudApi) private readonly qcloudApi: IQCloudApi,
        @inject(IViewModelManager) private readonly viewModelManager: IViewModelManager,
        @inject(IViewModel) private readonly viewModel: IViewModel,
        @inject(IFileExplorer) private readonly fileExplorer: IFileExplorer,
        @inject(INotification) private readonly notification: INotification,
    ) { }
    public async execute(param: ICreateModelParam) {
        const parentObject = this.viewModelManager.findObject(param.parentObjectId);
        if (!parentObject) {
            throw createError({ message: `parent object can't be found: ${param.parentObjectId}`, type: "technical" });
        }
        switch (parentObject.objectType) {
            default: throw createError({ message: `${parentObject.objectType} can not be parent`, type: "technical" });
            case "application": case "folder": case "module": break;
        }
        this.model = {
            name: param.name, ID: "", parent: parentObject, objectType: "model", synced: false, usageType: param.usageType,
            modelType: param.modelType, modelBody: param.modelBody, additionals: param.modelAdditionals, modified: {},
        };
        this.viewModelManager.modelCreated(this.model);
        if (param.duplicateFrom) {
            const executeRetval = await this.qcloudApi.duplicateModel(param.duplicateFrom, this.model.name);
            this.model.ID = executeRetval.ID;
        }
        else {
            const executeRetval = await this.qcloudApi.createModel({
                name: this.model.name,
                parentObject: this.model.parent,
                modelBody: this.model.modelBody,
                modelType: param.modelType,
                modelAdditionals: param.modelAdditionals,
                usageType: param.usageType,
            });

            this.model.ID = executeRetval.ID;
        }
        this.model.synced = true;
        //This line should be here while the "synced" feature doesn't work on "fileExplorer".
        //Then move it before the "this.qcloudApi.createModel" line.
        this.addToFileExplorer();

        this.notification.showNotification({ text: `${this.model.name} created successfully`, type: "success" });
        return this.model;
    }
    public async fail(err: unknown) {
        if (!this.model) { return; }

        this.viewModelManager.removeModel(this.model);
    }

    private addToFileExplorer() {
        const model = this.model!
        let filterType = modelFilterType[model.modelType];
        if (this.viewModel.studio.filters[filterType] === false) { return; }

        this.fileExplorer.addNewItem(model, model.parent.ID);
    }
}