type WatchSetter<TObj, TField extends keyof TObj> = (newValue: TObj[TField], oldValue: TObj[TField]) => void;
type WatchGetter<TObj, TField extends keyof TObj> = (value: TObj[TField]) => TObj[TField];


export class Watcher<TObj, TField extends keyof TObj>{
    private changeWatchers?: Array<WatchSetter<TObj, TField>>;
    private getWatchers?: Array<WatchGetter<TObj, TField>>;
    private val: TObj[TField];
    constructor(private readonly obj: TObj, private readonly field: TField, options: { preventSameInstance?: boolean } = {}) {
        this.val = obj[field];
        Object.defineProperty(obj, field, {
            set: v => {
                if (!this.changeWatchers) { this.val = v; return; }

                const old = this.val;
                this.val = v;
                if (options.preventSameInstance && old == this.val) { return; }

                this.changeWatchers.forEach(watcher => watcher(this.val, old));
            },
            get: () => {
                if (!this.getWatchers) { return this.val; }

                return this.getWatchers.reduce((prev, curGetter) => curGetter(prev), this.val);
            },
            configurable: true,
        });
    }

    public onChange(cb: WatchSetter<TObj, TField>) {
        this.changeWatchers = this.changeWatchers || [];
        this.changeWatchers.push(cb);
    }
    public onGet(cb: WatchGetter<TObj, TField>) {
        this.getWatchers = this.getWatchers || [];
        this.getWatchers.push(cb);
    }
    public unwatch() {
        delete this.obj[this.field];
        this.obj[this.field] = this.val;
    }
}