import { Logger, LogType } from "./logger";
import { IDictionary } from "../interfaces/IDictionary";
import { Singularizer } from "./singularizer";

const undefinedType: string = "undefined";

interface EventListenerCollection {
    origAddEventListener: any,
    origRemoveEventListener: any,
    activeListeners: (IDictionary<Array<EventListenerOrEventListenerObject>>),
    eventTarget: any
}

export interface IDocument extends Partial<Document> {
    addEventListener(type: string, listener: EventListenerOrEventListenerObject, options?: boolean | AddEventListenerOptions): void;
    createElement(tagName: string, options?: ElementCreationOptions): HTMLElement;
}

export interface IHistory extends Partial<History> {
    pushState(data: any, title: string, url?: string | null): void;
}


export interface IWindow extends Partial<Window> {

}

export class WindowHelper {

    private doc: IDocument;
    private hist: IHistory;
    private win: IWindow;
    private eventListeners: IDictionary<EventListenerCollection> | undefined;

    constructor() {
        this.doc = typeof (document) !== undefinedType ? document : {
            addEventListener(type: string, listener: EventListenerOrEventListenerObject, options?: boolean | AddEventListenerOptions) {
                //might simulate this in future...
                Logger.Log({ logMessage: `Enviroment does not support document. Triggering document.addEventListener now, type is: ${type}`, logType: LogType.Warning });
                (<EventListener>listener)(<any>{target: {readyState: this.readyState}});
            },
            createElement(tagName: string, options?: ElementCreationOptions): HTMLElement {
                return <any>{ tagName, options };
            },
            readyState: "complete"
        };

        this.hist = typeof (history) !== undefinedType ? history : {
            pushState(data: any, title: string, url?: string | null) {
                //might log data and url with additional data in logging
                Logger.Log({ logMessage: `history.pushState not supported on Node. title: ${title}`, logType: LogType.Debug });
            }
        };

        this.win = typeof (window) !== undefinedType ? window : {
            setInterval: setInterval,
            clearInterval: clearInterval,
            setTimeout: setTimeout,
            clearTimeout: clearTimeout,
            atob: atob,
            btoa: btoa
        };

        // this.eventListeners = this.wrapEventListeners();
    }
    public set onPopState(value: ((ev: PopStateEvent) => any) | null) {
        if (typeof (window) !== undefinedType) {
            window.onpopstate = value
        } else {
            Logger.Log({ logMessage: "onPopState not supported on Node.", logType: LogType.Debug });
        }
    }
    public get document() {
        return this.doc;
    }
    public get history() {
        return this.hist;
    }
    public get Window() {
        return this.win;
    }
    public static Instance: WindowHelper = Singularizer.trySingularize("theOneWindow", new WindowHelper().Window, new WindowHelper());


    private wrapEventListeners(): IDictionary<EventListenerCollection> {
        const self: any = this;
        var eventListeners: IDictionary<EventListenerCollection> = {
            documentEventListeners: {
                origAddEventListener: self.document.addEventListener,
                origRemoveEventListener: self.document.removeEventListener,
                eventTarget: self.document,
                activeListeners: {}
            },
            windowEventListeners: {
                origAddEventListener: self.Window.addEventListener,
                origRemoveEventListener: self.Window.removeEventListener,
                eventTarget: self.Window,
                activeListeners: {}
            }
        }

        Object.keys(eventListeners).forEach(function (collectionName) {
            var eventListenerCollection = (<any>eventListeners[collectionName]);
            if (!eventListenerCollection) { return; }
            eventListenerCollection.eventTarget.addEventListener = function (type: string | number, listener: EventListenerOrEventListenerObject, options: any) {
                eventListenerCollection.eventTarget = this;
                addEventListener(eventListenerCollection, type, listener, options);
            };
            eventListenerCollection.eventTarget.removeEventListener = function (type: string, listener: EventListenerOrEventListenerObject, options: any) {
                eventListenerCollection.eventTarget = this;
                removeEventListener(eventListenerCollection, type, listener, options);
            };

        });

        var addEventListener = (eventListenerCollection: any, type: string | number, listener: EventListenerOrEventListenerObject, options: any) => {
            if (!eventListenerCollection.activeListeners[type]) { eventListenerCollection.activeListeners[type] = []; }
            eventListenerCollection.activeListeners[type].push(listener);
            eventListenerCollection.origAddEventListener.apply(eventListenerCollection.eventTarget, [type, listener, options])
        }

        var removeEventListener = (eventListenerCollection: any, type: string, listener: any, options: any) => {
            if (!eventListenerCollection.activeListeners[type]) { return; }
            let listenerIndex = eventListenerCollection.activeListeners[type].findIndex((item: any) => item == listener);
            eventListenerCollection.activeListeners[type].splice(listenerIndex, 1);
            eventListenerCollection.origRemoveEventListener.apply(eventListenerCollection.eventTarget, [type, listener, options]);
        };

        return eventListeners;
    }
}