export type ObjectID = string; // GUID
export type AuthenticationTypes = ["QCLOUDAUTH", "ADFS"];
export type ModelHistoryType = "update" | "delete" | "checkin" | "publish" | "ownerItemDelete";
export type ApplicationObjectType = "application";
export type ModuleObjectType = "module";
export type FolderObjectType = "folder";
export type ModelObjectType = "model"
export type AppSettingsObjectType = "appSettings";
export type QJsonType = "qjson" | "template" | "pageTemplate" | "quickComponent";
export type ModelResponseType = "info" | "body" | "full";
export type ContentType = "qjson" | "js" | "yaml" | "css";
export type ModelType = ContentType | "bpmn" | "process" | "entityDesigner" | "namedComponent";
export type UsageType = AppSettingsObjectType;
export type ModuleType = "basic";

export const UISettingsType = {
    alert: "Alert Component",
    pipeline: "Pipeline",
    loading: "Loading Component",
    settings: "Settings File",
    localProxy: "Local Proxy File",
    contSvc: "Container Services File",
    css: "Style File",
    compList: "Component List",
    globalLocalization: "Global Localization",
} as const;
export type UISettingsType = typeof UISettingsType[keyof typeof UISettingsType];

interface ICloudObject {
    ID: ObjectID;
    name: string;
    ownerUser: ObjectID;
    createDate: Date;
    createdBy: ObjectID;
    updateDate?: Date;
    updatedBy?: ObjectID;
    storeInfo?: IStoreInfo;
    isPermanent?: boolean;
    priority?: number;
}

export interface IStoreInfo {
    storeID: ObjectID;
    useDate: Date;
}

export interface IApplicationDeployParameters {
    gitProductRepoId: number;
    gitGroupId: number;
    gitServiceRepoId: number;
}

export interface IApplicationDetails {
    moduleCount: number
    pageCount: number;
    pageTemplateCount: number;
    templateCount: number;
    processCount: number;
    entityCount: number;
}

export interface IModuleDeployParameters {
    gitGroupId: number;
    gitServiceRepoId: number;
}

export interface IApplication extends ICloudObject {
    objectType: ApplicationObjectType;
    logo?: string;
    color?: string;
    description?: string;
    deployParameters?: IApplicationDeployParameters;
}

export interface IModuleRelatedApplicationItem {
    applicationID: ObjectID;
}

export interface IModule extends ICloudObject {
    description?: string;
    objectType: ModuleObjectType;
    relatedApplications: Array<IModuleRelatedApplicationItem>;
    deployParameters?: IModuleDeployParameters;
    moduleType?: ModuleType;
}

export interface IModelCopyInfo {
    sourceModelID: ObjectID;
    date: Date;
    user: string;
}

export interface IModelInfo extends ICloudObject {
    objectType: ModelObjectType,
    ownerItem: ObjectID;
    ownerType: ApplicationObjectType | ModuleObjectType | FolderObjectType;
    modelType: ModelType;
    modelAdditionals: IQJsonAdditionals | IProcessManagerAdditionals | IEntityDesignerAddtionals;
    checkouts?: Array<IModelCheckout>;
    usageType?: UsageType;
    copyFromInfo?: IModelCopyInfo;
    size?: number;
}

export interface IFolder extends ICloudObject {
    objectType: FolderObjectType,
    ownerItem: ObjectID;
    ownerType: ApplicationObjectType | ModuleObjectType | FolderObjectType;
}


export interface IModelBodyReqObject {
    key: string;
    model: any;
}

export interface IModelBody extends IModelBodyReqObject {
    ID: ObjectID;
    modelInfoId: ObjectID;
    createDate: Date;
    size?: number;
}



export interface IModelCheckout {
    user: string;
    date: Date;
}

export interface IQJsonAdditionals {
    qjsonType: QJsonType;
    image?: string;
    isRootPage?: boolean;
}

export interface IProcessManagerAdditionals {
    token?: string;
    configEndpoint?: string;
}

export interface IEntityDesignerAddtionals {
    token?: string;
    configEndpoint?: string;
}





export interface IModelHistoryInfo extends ICloudObject, IModelInfo {
    modelID: ObjectID;
    historyType: ModelHistoryType;
    version?: string;
    shortComment?: string;
    comment?: string;
}

export interface IModelHistoryBody extends IModelBodyReqObject {
    ID: ObjectID;
    modelHistoryId: ObjectID;
    createDate: Date;
}



export interface IQCloudLogItem {
    ID?: string;
    detail: any;
    stack?: string;
    state?: { app: { ID: string, name: string }, openItemIDs?: Array<string>, currentItemID?: string }
    level: "debug" | "info" | "log" | "trace" | "warning" | "error";
    type: "business" | "technical";
}

export interface IQCloudLogDBItem extends IQCloudLogItem {
    date: Date;
    user?: { ID: ObjectID, name?: string, email: string };
}


export interface IUser extends ICloudObject {
    mailAddress: string,
    password: string,
    groups: Array<IGroup>;
}

export interface IGroup extends ICloudObject {
    objectType: "group";
    authorizationDefinitionNames: Array<string>;
}

export interface IPage extends ICloudObject {
    objectType: "page",
}

export interface IOperation extends ICloudObject {
    objectType: "operation",
}


export interface IAuthorizationDefinition extends ICloudObject {
    authPages: Array<IPage>,
    authOperations: Array<IOperation>,
    authTypes: "ALL" | "NONE" | "OPTIONAL"
}

export interface ITenantUser extends ICloudObject {
    tenantRelations: Array<ITenantRelation>
}
export interface ITenantRelation {
    tenantID: ObjectID,
    loginType: AuthenticationTypes,
    loginParameters: Record<string, any>
}

export interface ITenantDefinitions extends ICloudObject {
    value: any;
    isActive: boolean;
    objectType?: AppSettingsObjectType;
    targetObjectType?: ApplicationObjectType;
    contentType?: ContentType;
    description?: string;
}

export type ItemLimitActionType = "deleteOldest" | "stopAdding";

export interface IItemLimitationDetail {
    value: number;
    action?: ItemLimitActionType;
}

export interface IItemLimitations {
    history: {
        maxCountPerModel: IItemLimitationDetail;
        maxCountPerModule: IItemLimitationDetail;
        maxCountPerApplication: IItemLimitationDetail;
        maxCountPerTenant: IItemLimitationDetail;
    };
    model: {
        maxCountPerModule: IItemLimitationDetail;
        maxCountPerApplication: IItemLimitationDetail;
        maxCountPerTenant: IItemLimitationDetail;
    };
    module: {
        maxCountPerApplication: IItemLimitationDetail;
        maxCountPerTenant: IItemLimitationDetail;
    };
    application: {
        maxCountPerTenant: IItemLimitationDetail;
    };
}

