export interface IDomain extends ICloudObject {
    objectType: "tenant";
    applications: Record<"quick" | "entityDesigner" | "processManager", boolean>;
    dbName: string;
    itemLimitations?: IItemLimitations;
}

export type ITreeviewItemType = ModelType | "module" | "folder";

export interface ITreeviewItem {
    id: string;
    parentId?: string;
    name: string;
    priority: number;
    type: ITreeviewItemType;
    createDate: Date;
    shortName?: string;
    modelAdditionals?: IQJsonAdditionals | IProcessManagerAdditionals | IEntityDesignerAddtionals;
    children?: Array<ITreeviewItem>;
    moduleType?: ModuleType;
    usageType?: UsageType;
    checkouts?: Array<IModelCheckout>;
    deployParameters?: IModuleDeployParameters;
    relatedApplications?: IModule["relatedApplications"];
    size?: number;
}

export interface IScreenItem {
    id: string,
    fileName: string,
    ownerItem: string
}

export interface IApplicationCard extends IApplication {
    moduleCount: number;
    pageCount: number;
    templateCount: number;
}

export interface IPimQJsonOptions {
    qJsonType?: QJsonType
}

export type GeneratePageType = "create" | "list" | "update";

export interface IIntegration extends ICloudObject {
    theme: string,
    settings: {
        engine: {
            version: string,
            babel: boolean,
            embedded: boolean,
            multitab: boolean,
            plugins: Array<{
                Native: [
                    {
                        name: string,
                        text: string,
                        dependencies: Array<string>,
                        enabled: boolean
                    }],
                Quick: [{
                    name: string,
                    text: string,
                    dependencies: Array<string>,
                    enabled: boolean
                }]
            }>,
            region: string,
            backend: string
        },
        backends: Array<string>,
        configuration: any  //IWebSDKSettings
    },
    componentList: Record<string, Record<string, {
        "type": string,
        "required": boolean,
        "editorDefaultValue": string,
        "runTimeOverrideValue": string,
        "runTimeDefaultValue": string,
        "options": Array<string>
    }>>
}

