
import { UISettingsType } from "@stechquick/algae/lib/qCloudTemp/quickCloud";
import { ISocketPackage, PModules } from "@stechquick/plateau-comm";
import { IModelSate } from "@stechquick/plateau-comm/dist/common/shrimp/interfaces/quick/IModelState";
import { INamedCompModel, INamedCompModelOnly } from "@stechquick/plateau-comm/dist/common/shrimp/interfaces/quick/INamedCompModel";
import { IQJsonAdditionals, ModelType } from "@stechquick/plateau-comm/dist/common/shrimp/interfaces/quickCloud/quickCloud";
import { container, inject, injectable } from "../../../domain/core/diContainer";
import { IConfig } from "../../../domain/infrastructure/IConfig";
import { IFrameMessanger } from "../../../domain/infrastructure/IFrameMessanger";
import { IQCloudApi } from "../../../domain/infrastructure/IQCloudApi";
import { IApplication, IFolder, IModel, IModelBodyObject, IModelModifyInfo, IModule } from "../../../domain/model/models";
import { IEditor } from "../../../domain/objects/editors/IEditor";
import { IQuickEditor } from "../../../domain/objects/editors/IQuickEditor";
import { IEditorManager } from "../../../domain/objects/IEditorManager";
import { IAsyncComponentCreator } from "../../../domain/presentation/IAsyncComponentCreator";
import { IDialog } from "../../../domain/presentation/IDialog";
import { IModelBodyHelper } from "../../../domain/useCase/IModelBodyHelper";
import { IModelSelector } from "../../../domain/useCase/IModelSelector";
import { IViewModel } from "../../../domain/viewModel/IViewModel";
import { IViewModelManager } from "../../../domain/viewModel/IViewModelManager";

type PModulesSettingsDict = NonNullable<PModules["Quick"]["setModel"]["settings"]>;
@injectable()
export class QuickEditorImpl implements IQuickEditor {
    public name = "quick" as "quick";
    private connected: boolean = false;
    public get readyProm() {
        return this.frameMessanger.readyProm;
    }

    constructor(
        @inject(IViewModel) private readonly viewModel = container.resolve<IViewModel>(IViewModel),
        @inject(IViewModelManager) private readonly viewModelManager = container.resolve<IViewModelManager>(IViewModelManager),
        @inject(IFrameMessanger) private readonly frameMessanger = container.resolve<IFrameMessanger>(IFrameMessanger),
        @inject(IConfig) private readonly config = container.resolve<IConfig>(IConfig),
        @inject(IQCloudApi) private readonly qcloudApi = container.resolve<IQCloudApi>(IQCloudApi),
        @inject(IModelBodyHelper) private readonly modelBodyHelper = container.resolve<IModelBodyHelper>(IModelBodyHelper),
    ) {
        this.connect();
    }
    public connect() {
        if (this.connected) { return; }
        this.frameMessanger.connect(this.config.getValue("studio").editors.quick, message => {
            switch (message.msg.type) {
                case "addComponentToScreen":/*şimdilik geç*/break;
                case "addOrUpdateNamedComp": this.addOrUpdateNamedComp({ id: message.id, msg: message.msg }); break; // TODO: namedComponent'in kendi editörü olmalı. quick -> IDE
                //case "createGoAction": /* burada da quick içinde de silinecek. openPageWizard'ın reply'ı bu */ break; 
                case "generatePageEntityModel":/*şimdilik geç*/break;
                case "getNamedComp": this.getNamedComponent({ id: message.id, msg: message.msg }); break; // TODO: namedComponent'in kendi editörü olmalı. quick -> IDE
                case "getQuickComp": this.getQuickComp({ id: message.id, msg: message.msg }); break; // çek dön
                case "IAmReady": this.iamReady({ id: message.id, msg: message.msg }); break;
                case "modifiedRequest": this.modifiedRequest({ id: message.id, msg: message.msg }); break;
                case "openPageWizard": this.openPageWizard({ id: message.id, msg: message.msg }); break; // sayfa listesi seç ve dön. //quick içinde çağıran yer await'le çözülecek. createGoAction yerine
            }
        }, { init: true });
        this.connected = true;
    }
    public attach(element: HTMLElement) {
        this.connect();
        this.frameMessanger.attach(element);
    }
    public detach() {
        this.frameMessanger.detach();
    }
    public show(show: boolean): void {
        this.frameMessanger.show(show);
    }
    public async getModel(): Promise<{ model: Array<IModelBodyObject>; state: any }> {
        const resp = await this.frameMessanger.sendMessage(
            "Quick",
            "getModel",
            { type: "getModel" },
            { awaitResponse: true }
        );
        const getModelResponse = resp?.msg as PModules["Quick"]["getModelResponse"];
        return {
            model: [{ key: "qjson", model: getModelResponse.content }],// will change when quick compiled and source is seperated
            state: getModelResponse.state,
        };
    }
    public async setModel(item: IModel) {
        const settings = this.viewModel.studio.items
            .filter((item): item is IModel => item.objectType == "model" && item.usageType == "appSettings")
            .reduce<PModulesSettingsDict>((prev, item) => {
                switch (item.name) {
                    case UISettingsType.alert: prev["alert.qjson"] = this.modelBodyHelper.getModelBody(item, "qjson")?.model; break;
                    case UISettingsType.pipeline: prev["pipeline.qjson"] = this.modelBodyHelper.getModelBody(item, "qjson")?.model; break;
                    case UISettingsType.loading: prev["loading.qjson"] = this.modelBodyHelper.getModelBody(item, "qjson")?.model; break;
                    case UISettingsType.settings: prev["settings.yaml"] = this.modelBodyHelper.getModelBody(item, "yaml")?.model; break;
                    case UISettingsType.localProxy: prev["localProxy.yaml"] = this.modelBodyHelper.getModelBody(item, "yaml")?.model; break;
                    case UISettingsType.contSvc: prev["containerServices.js"] = this.modelBodyHelper.getModelBody(item, "js")?.model; break;
                    case UISettingsType.compList:  prev["componentList.js"] = this.modelBodyHelper.getModelBody(item, "js")?.model; break;
                    case UISettingsType.css: prev["main.css"] = this.modelBodyHelper.getModelBody(item, "css")?.model; break;
                    case UISettingsType.globalLocalization: prev["globalLocalization.qjson"] = this.modelBodyHelper.getModelBody(item, "qjson")?.model; break;
                }
                return prev;
            }, {});

        await this.frameMessanger.sendMessage("Quick", "setModel",
            {
                content: item.modelBody?.[0].model, // will change when quick compiled and source is seperated
                fileName: item.name, type: "setModel", state: item.state, settings
            }, { awaitResponse: true }
        );

        this.setQuickCompList(item);
        this.setNamedComps();
        
        //TODO: setProperties
    }

    private setQuickCompList(targetModel: IModel) {
        const recursiveScopedFinder = (item: IFolder | IModule | IApplication) => {
            if(item.objectType == "module" || item.objectType == "application"){
                return item;
            }
            const parent = this.viewModelManager.findObject(item.ID) as IFolder | IModule | IApplication;
            recursiveScopedFinder(parent);
        }
        
        const scopedParentItem = recursiveScopedFinder(targetModel.parent)

        const params: Array<PModules["Quick"]["setQuickComps"]["params"][0]> = [];
        scopedParentItem!.children!.forEach(item => {
            if (
                item.objectType == "folder"
                || item.objectType == "module"
                || item.modelType != "qjson"
                || item.ID == targetModel.ID
                || (item.additionals as IQJsonAdditionals).qjsonType != "quickComponent"
            ) {
                return;
            }

            const qjsonAdd = item.additionals as IQJsonAdditionals;
            params.push({
                name: item.name,
                id: item.ID,
                qJsonPath: `'<<qjson:${item.ID}>>'`,
                qJsonOptions: {
                    qJsonType: qjsonAdd.qjsonType
                }
            })
        });

        this.frameMessanger.sendMessage("Quick", "setQuickComps", { removeOlds: true, type: "setQuickComps", params }, {});
    }

    private setNamedComps(){
        const namedComps = this.viewModel.studio.items
        .filter((item): item is IModel => item.objectType == "model" && item.modelType == "namedComponent")
        .map(item => {
            const retVal = item.modelBody![0].model as INamedCompModelOnly;
            (retVal.pim as any).Component.CompPath = `<<named:${item.ID}>>`;
            return retVal;
        });
        this.frameMessanger.sendMessage("Quick", "setNamedCompList", {type:"setNamedCompList",compModelList: namedComps},{});
    }

    private async addOrUpdateNamedComp(message: ISocketPackage<PModules["Quick"]["addOrUpdateNamedComp"]>) {
        const sendReply = (success: boolean, replyId: string, compPath?: string, message?: string) => this.frameMessanger.sendMessage("Quick", "addOrUpdateNamedCompResponse",
            { type: "addOrUpdateNamedCompResponse", success, compPath, message }, { replyId });
        const createModel: (model: INamedCompModel) => Array<IModelBodyObject> = model =>
            [{ key: "pim", model: model.pim }, { key: "files", model: model.files }];
        
        const parentObject = this.viewModelManager.findObject(this.viewModel.studio.appId) as IApplication;
        let compPath = message.msg.compPath;
        try {
            if (compPath) {
                const regExp = new RegExp('<<named:(\\w(?![/]).*)>>');
                const id = regExp.exec(compPath)![1];

                await this.qcloudApi.updateModel(id, { model: createModel(message.msg.compModel) });
            } else {
                const resp = await this.qcloudApi.createModel({
                    name: message.msg.compName, modelType: "namedComponent", parentObject, modelBody: createModel(message.msg.compModel)
                });
                compPath = `<<named:${resp.ID}>>`;
            }
            sendReply(true, message.id, compPath);
        } catch (ex) {
            sendReply(false, message.id, compPath, (ex as Error).message);

            throw ex;
        }
    }
    private async getNamedComponent(message: ISocketPackage<PModules["Quick"]["getNamedComp"]>) {
        let compPath = message.msg.compPath;
        const regExp = new RegExp('<<named:(\\w(?![/]).*)>>');
        const id = regExp.exec(compPath)![1];
        const resp = await this.qcloudApi.getModelBody(id);
        const compModel = resp.model!.reduce<INamedCompModel>((prev, item) => {
            if (item.key == "pim"){ prev.pim = item.model; }
            else if (item.key == "files") { prev.files = item.model; }
            return prev;
        }, {} as any);
        this.frameMessanger.sendMessage("Quick", "getNamedCompResponse", { type: "getNamedCompResponse", compModel }, { replyId: message.id });
    }
    private async getQuickComp(message: ISocketPackage<PModules["Quick"]["getQuickComp"]>) {
        let qjsonPath = message.msg.qjsonPath;
        const regExp = new RegExp('<<qjson:(\\w(?![/]).*)>>');
        const id = regExp.exec(qjsonPath)![1];
        const object = this.viewModelManager.findObject(id);
        let modelBody: Array<IModelBodyObject> | undefined;
        if (object?.objectType == "model" && object.modelBody) {
            modelBody = object.modelBody;
        } else {
            const resp = await this.qcloudApi.getModelBody(id);
            modelBody = resp.model;
        }
        const qjson = modelBody?.[0].model// will change when quick compiled and source is seperated
        this.frameMessanger.sendMessage("Quick", "getQuickCompResponse", { type: "getQuickCompResponse", qjson }, { replyId: message.id });
    }
    private modifiedRequest(message: ISocketPackage<PModules["Quick"]["modifiedRequest"]>) {
        const currentItem = this.viewModel.studio.currentItem;
        if (!currentItem) { return; }

        currentItem.modified = {state: true, model: true}; // TODO: Get modified info from quick modify info message.
    }
    private iamReady(message: ISocketPackage<PModules["Quick"]["IAmReady"]>) {
        console.log("iam ready came from quick");
        this.frameMessanger.sendMessage("Quick", "IAmReadyResponse", {
            type: "IAmReadyResponse",
            editorButtons: {
                file: false,
                createNamedComponent: true,
                pageWizard: true,
                addStyle: false
            },
            qjsonHandling: {
                value: "<<qjson:",
                type: "prefix" // regex maybe..
            }
        }, {});
    }
    private openPageWizard(message: ISocketPackage<PModules["Quick"]["openPageWizard"]>){
        const modelSelector = container.resolve<IModelSelector>(IModelSelector);
        modelSelector.openModelSelector((item: IModel) => {
            const qjsonPath = `<<qjson:${item.ID}>>`;
            this.frameMessanger.sendMessage("Quick", "openPageWizardResponse", { type: "openPageWizardResponse", qjsonPath }, { replyId: message.id });
        });
    }

}