import { injectable, inject } from "../../domain/core/diContainer";
import { IQCloudApi } from "../../domain/infrastructure/IQCloudApi";
import {
    IModule,
    IModuleSettingsItem,
    IApplicationSettingsItem,
    IApplication,
} from "../../domain/model/models";
import { createError } from "../../domain/model/shellError";
import { INotification } from "../../domain/presentation/INotification";
import { IViewModelManager } from "../../domain/viewModel/IViewModelManager";
import { IApplicationSettings } from "../../domain/useCase/IApplicationSettings";
import { IGitResponse, IProviderApi } from "../../domain/infrastructure/IProviderApi";
import { IQCloudBaseResponse } from "../../infrastructure/qCloudApiImpl";

@injectable()
export class ApplicationSettingsImpl implements IApplicationSettings {
    constructor(
        @inject(IQCloudApi) private readonly qcloudApi: IQCloudApi,
        @inject(IProviderApi) private readonly providerApi: IProviderApi,
        @inject(IViewModelManager) private readonly viewModelManager: IViewModelManager,
        @inject(INotification) private readonly notification: INotification
    ) {}
    public async update(settings: {
        applicationSettings?: IApplicationSettingsItem;
        modulesSettings?: Array<IModuleSettingsItem>;
    }) {
        const { applicationSettings, modulesSettings } = settings;
        if (!applicationSettings && !modulesSettings?.length) {
            throw createError({
                message: `At least one setting must be provided.`,
                type: "business",
            });
        }
        this.qcloudApi.updateApplicationSettings({ applicationSettings, modulesSettings });

        if (applicationSettings) {
            const app = this.viewModelManager.findObject(
                applicationSettings.applicationID
            ) as IApplication;
            app.deployParameters = applicationSettings.deployParameters;
            this.viewModelManager.objectUpdated(app);
        }
        if (modulesSettings) {
            modulesSettings.forEach((newModuleSettings) => {
                const module = this.viewModelManager.findObject(
                    newModuleSettings.moduleID
                ) as IModule;
                module.deployParameters = newModuleSettings.deployParameters;
                this.viewModelManager.objectUpdated(module);
            });
        }
        this.notification.showNotification({
            text: "Settings updated successfully",
            type: "success",
        });
    }

    getProjectsByGroupId(groupId: number): Promise<IQCloudBaseResponse<IGitResponse>> {
        const projects = this.providerApi.getProjectsByGroupId(groupId);
        return projects;
    }

    getProjectsOfProduct(): Promise<IQCloudBaseResponse<IGitResponse>> {
        const products = this.providerApi.getProjectsOfProduct();
        return products;
    }

    getGroupNamesWithId(): Promise<IQCloudBaseResponse<IGitResponse>> {
        const products = this.providerApi.getGroupNamesWithId();
        return products;
    }
}
