export class UrlHelper {
    private static queryStringData?: any;
    public static gatherQueryString<T = Record<string, string | undefined>>() {
        return (this.queryStringData as T) || (this.queryStringData = window.location.search.replace(/\?/g, "&").split("&")
            .map(item => { const dat = item.split("="); return { key: dat[0], value: dat[1] } })
            .reduce((prev, item) => { (prev as any)[item.key] = item.value; return prev; }, {} as T));
    }
}