import { IEntityDesignerAddtionals, IProcessManagerAdditionals, IQJsonAdditionals } from "@stechquick/algae/lib/qCloudTemp/quickCloud";
import { container, inject, injectable } from "../../domain/core/diContainer";
import { IQCloudApi } from "../../domain/infrastructure/IQCloudApi";
import { IModule, IApplication, IModel, IFolder } from "../../domain/model/models";
import { createError } from "../../domain/model/shellError";
import { INotification } from "../../domain/presentation/INotification";
import { ITreeView, ITreeViewInstances } from "../../domain/presentation/ITreeView";
import { IFileExplorer } from "../../domain/useCase/IFileExplorer";
import { IObjectUseCase } from "../../domain/useCase/IObjectUseCase";
import { IStudio } from "../../domain/useCase/IStudio";
import { IViewModelManager } from "../../domain/viewModel/IViewModelManager";

@injectable()
export class ObjectUseCaseImpl implements IObjectUseCase {
    constructor(
        @inject(INotification) private readonly notification: INotification,
        @inject(IViewModelManager) private readonly viewModelManager: IViewModelManager,
        @inject(IQCloudApi) private readonly qcloudApi: IQCloudApi,
        @inject(IStudio) private readonly studio: IStudio,
        @inject(IFileExplorer) private readonly fileExplorer: IFileExplorer,
    ) { }
 
    public async deleteObject(objectID: string) {
        const object = this.viewModelManager.findObject(objectID);

        if (!object){ throw createError({message: "object not found: " + objectID, type: "technical"}); }
        switch (object.objectType) {
            case "application": case "module": case "folder": case "model": break;
            default: throw createError({message: "can not delete " + object?.objectType + " with this use case ",type:"technical"});
        }

        const item = object;
        const confirm = await this.notification.showConfirm({
            title: "Are you sure?", text: `The ${item.objectType} '${item.name}' will be deleted permanently!`,
            type: "warning", approveText: "Delete", rejectText: "Cancel"
        });
        if (!confirm.approved) { return; }

        if (item.objectType == "model") {
            this.studio.closeItem(item, { silent: true });
        }
        const targetDict = {
            model: this.qcloudApi.deleteModel,
            module: this.qcloudApi.deleteModule,
            folder: this.qcloudApi.deleteFolder,
            application: this.qcloudApi.deleteApplication,
        };
        await targetDict[item.objectType].call(this.qcloudApi, item.ID, true);
        if (container.isRegistered(ITreeViewInstances.FileExplorer)) {
            const feTreeview = container.resolve<ITreeView>(ITreeViewInstances.FileExplorer);
            feTreeview.removeItem(item.ID);
        }
        this.viewModelManager.removeObject(item);
        this.notification.showNotification({ text: `'${item.name}' deleted successfully`, type: "success" });
    }
    public async updateObject(objectID: string, params:{name?: string, description?: string, modelAdditionals?:IQJsonAdditionals | IProcessManagerAdditionals | IEntityDesignerAddtionals}){
        if(!params.name && !params.description){
            throw createError({
                message: "Name or Description is required!",
                type: "technical"
            });
        }

        const object = this.viewModelManager.findObject(objectID);
        let newObj = {...object} as typeof object;

        if (!object){ throw createError({message: "object not found: " + objectID, type: "technical"}); }

        switch (object.objectType) {
            case "model":
                const model = newObj as typeof object;
                model.additionals = params.modelAdditionals || model.additionals;
                await this.qcloudApi.updateModel(objectID, {name: params.name, modelAdditionals:params.modelAdditionals})
                this.fileExplorer.removeItem(objectID);
                this.fileExplorer.addNewItem(model, model.parent.ID);
                break;
                case "module":
                const module = newObj as typeof object;
                module.description = params.description;
                await this.qcloudApi.updateModule(objectID, {name: params.name, description: params.description})
                object.description = params.description;
                break;
            case "application":
                const application = newObj as typeof object;
                application.description = params.description;
                await this.qcloudApi.updateApplication(objectID, {name: params.name, description: params.description})
                object.description = params.description;
                break;
            case "folder":
                await this.qcloudApi.updateFolder(objectID, {name: params.name!})
                break;
            default: throw createError({message: "can not update " + object?.objectType + " with this use case ",type:"technical"});
        }
        if (params.name) {
            object.name = params.name
        }
        if (container.isRegistered(ITreeViewInstances.FileExplorer)) {
            const feTreeview = container.resolve<ITreeView>(ITreeViewInstances.FileExplorer);
            feTreeview.updateItemName(object.ID, object.name);
        }
        this.viewModelManager.objectUpdated(newObj as typeof object);
        this.notification.showNotification({ text: `Updated successfully`, type: "success" });
    }

    public async listModules(applicationID?: string) {
        const modules = await this.qcloudApi.listModules(applicationID);
        return modules;
    }

    public async setRootScreen(modelID: string){
        const model = this.viewModelManager.findObject(modelID) as IModel;
        if (!model){ throw createError({message: "object not found: " + model, type: "technical"}); }
        
        await this.qcloudApi.setRootScreen(modelID);
        this.notification.showNotification({ text: `${model.name} set as the start screen successfully`, type: "success" });
    }

}