import { IDeployJobItem } from "@stechquick/algae/lib/qCloudTemp/exporter";
import { inject, injectable } from "../../domain/core/diContainer";
import { IQCloudApi } from "../../domain/infrastructure/IQCloudApi";
import { IDeployJobs } from "../../domain/useCase/IDeployJobs";
import { INotification } from '../../domain/presentation/INotification';
import { IExporterApi } from "../../domain/infrastructure/IExporterApi";

@injectable()
export class DeployJobsImpl implements IDeployJobs {
    constructor(
        @inject(IQCloudApi) private readonly qcloudApi: IQCloudApi,
        @inject(INotification) private readonly notification: INotification,
        @inject(IExporterApi) private readonly exporterApi: IExporterApi,
    ) { }

    public async createDeployJobs(deployJobs: Array<IDeployJobItem>) {
        await this.qcloudApi.createDeployJobs(deployJobs);
        this.exporterApi.go(); // do not await, just notify and go
        // TODO: Viewmodel need to be updated.
        this.notification.showNotification({
            text: "Deploy jobs started successfuly.",
            type: "success",
        });
    }
}